import React from 'react';
import { Navbar, Nav, Image } from 'react-bootstrap';
import { Link } from 'gatsby';
import 'bootstrap/dist/css/bootstrap.min.css';

export default ({ img, headerText, menuButtons }) => (
  <Navbar collapseOnSelect expand="lg" fixed="top">
    <Navbar.Brand href="/">
      <Image
        src={img}
        width="60"
        height="60"
        alt="React Bootstrap logo"
        rounded
        fluid
      />
    </Navbar.Brand>
    <Navbar.Brand href="/">
      {headerText}
    </Navbar.Brand>
    <Navbar.Toggle />
    <Navbar.Collapse className="justify-content-end">
      <Nav>
        {
          menuButtons.map(el => (
            <Link className="nav-link" to={el.href}>
              {el.name}
            </Link>
          ))
        }
      </Nav>
    </Navbar.Collapse>
  </Navbar>
);
