import React from 'react';
import {
  Col, Container, Navbar, Row,
} from 'react-bootstrap';
import { social } from '../helpers/data';


const style = {
  padding: '7rem 0',
  backgroundColor: '#e9ecef',
};

function Instagram({ data }) {
  return (
    <Col>
      <h3>{data.text}</h3>
      <a href={data.href}> </a>
    </Col>
  );
}

export default ({ footerData }) => (
  <Navbar sticky="bottom" style={style}>
    <Container>
      <Row className="text-center">
        {
          footerData.map(el => (
            <Col>
              <h3>{el.name}</h3>
              {el.text}
            </Col>
          ))
        }
        <Instagram data={social.instagram} />
      </Row>
    </Container>
  </Navbar>
);
